<template>
<b-container class="mt-4">
  <b-row class="justify-content-center">
    <b-col cols="10">
      <b-alert :show="showAlert" variant="warning" >
       <label v-for="error in errores" class="w-100" :key="error.id">{{error}}</label>
      </b-alert>
    </b-col>
  </b-row>
  <b-row class="justify-content-center">
    <b-col cols="12" lg="6">
      <b-form class="form-login bg-light" @submit="checkLogin">
        <b-form-group id="name" label="Tu nombre de usuario:" label-for="input-name">
          <b-form-input
            id="input-name"
            type="text"
            v-model="username"
            required
            placeholder="Ingrese nombre de usuario"
            ></b-form-input>
        </b-form-group>

        <b-form-group id="pass" label="Tu contraseña:" label-for="input-pass">
          <b-form-input
            id="input-pass"
            type="password"
            v-model="password"
            required
            placeholder="Ingrese contraseña"
            ></b-form-input>
        </b-form-group>
        <b-button type="submit" variant="dark" >Ingresar</b-button>
        <!-- <b-button type="reset" variant="danger">Reset</b-button> -->
      </b-form>
    </b-col>
  </b-row>
</b-container>
</template>

<script>

export default {
  name: 'Login',
  data () {
    return {
      username: '',
      password: '',
      users: this.$store.state.users,
      logueado: false,
      intentos: 0,
      intentosRestantes: 3,
      errores: [],
      showAlert: false
    }
  },
  methods: {
    checkLogin: function (e) {
      e.preventDefault()
      this.intentos++
      this.intentosRestantes--
      this.errores = []
      this.errores.push('Intentos'.concat(' ', this.intentos, '. Le quedan ', this.intentosRestantes, ' intentos'))
      this.errores.push('Error usuario o contraseña incorrecta')
      this.showAlert = true
      for (let user of this.users) {
        if (user.user === this.username && this.password === user.pass) {
          this.$localStorage.set('user', user.user)
          this.$localStorage.set('rol', user.rol)

          this.logueado = true
          this.$router.push('/')
          break
        } else {
          this.logueado = false
          this.user = ''
          this.pass = ''
          if (this.intentos === 3) {
            this.$router.push('/correo')
          }
        }
      }
    }
  }
}
</script>
