<template>
  <div class="mt-4">
    <input type="text" v-model="nuevaCarrera">
    <b-button @click="guardarNuevaCarrera()" class="m-2" variant="primary">Nueva carrera</b-button>
    <b-button @click="volver()" variant="secondary">Volver</b-button>
    <b-table striped hover :items="carreras" :fields="fields">
      <template slot="cursos" slot-scope="row">
        <b-button variant="success">Cursos</b-button>
      </template>
      <template slot="editar" slot-scope="row">
          <b-button @click.stop="row.toggleDetails" class="mr-1" @click="limpiar()">
         Editar
        </b-button>
      <!--     <b-button @click.stop="row.toggleDetails" variant="success">Editar</b-button> -->
      </template>

      <template slot="row-details" slot-scope="row">
      <b-row class="mb-2">
        <b-col cols="12" class="my-2 "> <b>Carrera:</b>
          <input name="" type="text" v-model="carreraItem"/>
        </b-col>

        <b-col cols="12" class="mt-2"><b-button variant="primary" @click="guardar(row.item)">Guardar</b-button></b-col>
      </b-row>
    </template>

      <template slot="eliminar" slot-scope="row">
        <b-button variant="danger" @click="imprimir(row.item.id)">Eliminar</b-button>
      </template>
    </b-table>
  </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex'

const fields = ['id', 'carrera', 'cursos', 'editar', 'eliminar']
export default {
  computed: mapState(['carreras']),
  
  data () {
    return {
      fields: fields,
      carreraItem: '',
      nuevaCarrera: ''
    }
  },
  methods: {
      imprimir: function(id){
          let ids = []
          for(let carrera of this.carreras ){
              ids.push(carrera.id)
          }
          let index = ids.indexOf(id)
          
          if(index > -1){
              this.carreras.splice(index, 1)
          }   
      }, 
      guardar: function(rowItem){
          rowItem.carrera = this.carreraItem
      },
      limpiar: function(){
          this.carreraItem = ''
      },
      guardarNuevaCarrera: function(){
          if(this.nuevaCarrera != ''){
            let ids = []
           for(let carrera of this.carreras ){
                ids.push(carrera.id)
            }
            let ultimoId = ids.splice(-1)[0]
          
            let carrera = {id: ultimoId+1, carrera: this.nuevaCarrera}
            this.carreras.push(carrera)
          }
      },
      volver: function() {
        this.$router.push('/')
      }
  },
}
</script>
